<template>
  <div id="app">
    <div class="container-fulid">
      <div class="row">
        <div class="col-8 content_left p-5">
          <div class="todowrap"><input type="text" placeholder="add a new mission..." @keyup.enter="itempush(item)" v-model="item"><span @click="itempush(item)">+</span></div>
    <div class="todocontent">
      <div><h1 class="item_h1"><i class="far fa-circle"  @click="compelete(index)"></i>{{todoitem[0]}}</h1><div class="clock">{{time.minutes>9?time.minutes:"0"+time.minutes}}:{{time.sec>9?time.sec:"0"+time.sec}}</div></div>
       <ul class="item_ul">
         <li v-for="(item,index) in todoitemlist" :key="index" class="itemto" ><i class="far fa-circle"  @click="compelete(index)"></i><span>{{item}}</span><i class="far fa-play-circle" @click="chageitem(index)"></i></li>
         <li><div class="more" @click="closeitem('list')">more</div></li>
       </ul>
       
      </div>
      </div>
       <div class="col-4 content_right">
         <i class="fas fa-list"  @click="closeitem('list')"></i>
         <i class="fas fa-chart-bar"  @click="closeitem('bar')"></i>
         <i class="fas fa-compact-disc"  @click="closeitem('disc')"></i>
       </div>
    </div>
    </div>
    <div class="timeclock" >
      <div class="time_bt " @click="timeclock()"></div>
      <div class="time_zero" @click="timezero"></div>
    </div>
    <transition 
    name=""
    enter-active-class="animated fadeInRight faster"
    leave-active-class="animated fadeOutRight faster"
  >
    <div class="itemlisttodo"  v-if="hide">
   <div class="row listtodo_wrap">
     <div class="col-5 listtodo_item" ><i class="fas fa-list" :class="{ltemactive:itemclick.list}" @click="changeitem('list')"><span>TO-DO LIST</span></i>
     <i class="fas fa-chart-bar" :class="{ltemactive:itemclick.bar}" @click="changeitem('bar')"><span>ANALYTICS</span></i>
     <i class="fas fa-compact-disc" :class="{ltemactive:itemclick.disc}" @click="changeitem('disc')"><span>RINGTONES</span></i></div>
     
     <div class="col-5">
         <transition 
    name=""
    enter-active-class="animated flipInX faster"
    leave-active-class="animated flipOutX faster"
     mode="out-in"
  >
       <div class="fa-list_content" v-if="itemclick.list" key="list">
          <div class="todowrap fa-list_todowrap"><input type="text" placeholder="add a new mission..." @keyup.enter="itempush(item)" v-model="item"><span @click="itempush(item)">+</span></div>
          <div class="fa-list_itemwrap">
             <h1>TO-DO</h1>
             <div class="fa-list_itemcontent">
               <ul class="fa_list_ul">
         <li v-for="(item,index) in todoitem" :key="index" class="fa_list_itemto fa_listtodo" ><i class="far fa-circle"  @click="compelete(index)"></i><span>{{item}}</span><i class="far fa-play-circle" @click="chagelistitem(index)"></i></li>
              </ul> 
             </div>
          </div>
            <div class="fa-list_itemwrap">
             <h1>Done</h1>
             <div class="fa-list_itemcontent">
               <ul class="fa_list_ul">
         <li v-for="(item,index) in compeleteitem" :key="index" class="fa_list_itemto" ><i class="fas fa-check-circle"  @click="compelete(index)"></i><span>{{item}}</span></li>
              </ul> 
             </div>
          </div>
       </div>
       <div class="fa-list_content"  v-if="itemclick.bar" key="bar">
         <div class="fa-list_itemwrap">
             <h1>FOCUS TIME</h1>
      <div class="focus_wrap">
      <div class="focustime_content">
        <h5>TODAY</h5>
        <p><span>20</span>/TOMATO</p>
      </div>
       <div class="focustime_content">
        <h5>Week</h5>
        <p><span>100</span>/TOMATO</p>
      </div>
      </div>
      </div>
       <div class="fa-list_itemwrap">
       <h1>CHART</h1>
       <hello :data={todoitem}></hello>
       </div>
       </div>
        <div class="fa-list_content"  v-if="itemclick.disc" key="disc">
         <div class="fa-list_itemwrap">
             <h1>WORK</h1>
        <div class="radiostyle">
         <input type="radio" id="work_none" name="work" checked="checked"><label for="work_none">NONE</label>
         <input type="radio"  id="work_ho" name="work"><label for="work_ho">CLOWN HORN</label>
        </div>
      </div>
       <div class="fa-list_itemwrap">
       <h1>BREAK</h1>
       <div class="radiostyle">
         <input type="radio" id="break_none" name="break"><label for="break_none">NONE</label>
         <input type="radio"  id="break_pa" name="break" checked="checked"><label for="break_pa">PASTORAL</label>
        </div>
       </div>
       </div>
    </transition>
     </div>
     <div class="col-2 item_close">
       <i class="fas fa-times" @click="closeitem"></i>
     </div>
   </div>
    </div>
    </transition>
  </div>
</template>

<script>
import hello from "./components/HelloWorld"
export default {
  name: 'App',
  components:{
   hello
  },
  data(){
  return {
    todoitem:["第一個項目","卡通娃偶","科學文學","地理歷史"],
    item:"",
    compeleteitem:[],
    time:{
      minutes:25,
      sec:0,
    },
    clocknum:"",
    hide:false,
    itemclick:{
      list:false,
      disc:false,
      bar:false,
    }
  }
  },methods:{
      itempush(val){
        if(val!=="")
        {
          this.todoitem.push(val);
          this.item="";
        }
      },
      compelete(index){
       var content=this.todoitem.splice(index,1);
        this.compeleteitem.push(content[0]);
      },
      chageitem(index){
        let vm=this;
        let itemto=document.querySelectorAll(".itemto");
        let itemh1=document.querySelector(".item_h1");
        itemto[index].classList.add("animated","zoomOutLeft","faster");
        itemh1.classList.add("animated","zoomOutLeft","faster");
        itemto[index].addEventListener("animationend", addzoom,false);
       function addzoom(){
          if(itemto[index].classList.contains("zoomOutLeft")){
              itemto[index].classList.replace("zoomOutLeft","zoomInRight");
               itemh1.classList.replace("zoomOutLeft","zoomInRight");
               let temp=vm.todoitem[index+1];
                vm.$set(vm.todoitem,index+1, vm.todoitem[0]);
                vm.$set(vm.todoitem,0, temp);
          }
          else{
            itemto[index].classList.remove("zoomInRight","animated","faster");
             itemh1.classList.remove("zoomInRight","animated","faster");
            itemto[index].removeEventListener('animationend', addzoom, false);
          }
       }
      },timeclock(){
        let vm=this.time;
        let vu=this;
        let timeclock=document.querySelector(".timeclock");
        let time_bt=document.querySelector(".time_bt");
        let time_zero=document.querySelector(".time_zero");
        if(timeclock.classList.contains("sotptimeclock")){
              timeclock.classList.remove("sotptimeclock");
          time_bt.classList.remove("time_stopbt");
          time_zero.classList.remove("time_stopzero");
               clearInterval(this.clocknum);
        }
        else
        {
           timeclock.classList.add("sotptimeclock");
            time_bt.classList.add("time_stopbt");
            time_zero.classList.add("time_stopzero");
        vu.clocknum=setInterval(function(){
             console.log(vu.clocknum);

            if(vm.sec==0&&vm.minutes==0){
               timeclock.classList.remove("sotptimeclock");
               time_bt.classList.remove("time_stopbt");
               time_zero.classList.remove("time_stopzero");
              clearInterval(vu.clocknum);
              vm.minutes=25;
              vm.sec=0;
            }
            else{
              if(vm.sec<=0){
               vm.minutes-=1;
               vm.sec=60;
               }
            
            vm.sec--;

            }
           
        },1000);
        }
        
      },timezero(){
         this.time.minutes=25;
         this.time.sec=0;
        let timeclock=document.querySelector(".timeclock");
        let time_bt=document.querySelector(".time_bt");
        let time_zero=document.querySelector(".time_zero");
        this.timestop(timeclock,time_bt,time_zero);
          clearInterval(this.clocknum);
      },timestop(timeclock,time_bt,time_zero){
          timeclock.classList.remove("sotptimeclock");
          time_bt.classList.remove("time_stopbt");
          time_zero.classList.remove("time_stopzero");
      },closeitem(e){
        this.hide=!this.hide;
        for(var i in this.itemclick)
        {
          if(i==e)
          {
             this.itemclick[i]=true;
          }
          else{
            this.itemclick[i]=false;
          }
        }
       
          
      },changeitem(e){
           for(var i in this.itemclick)
        {
          if(i==e)
          {
             this.itemclick[i]=true;
          }
          else{
            this.itemclick[i]=false;
          }
        }
        }, chagelistitem(index){
        let vm=this;
        let fa_listtodo=document.querySelectorAll(".fa_listtodo");
        fa_listtodo[index].classList.add("animated","zoomOut","faster");
        fa_listtodo[0].classList.add("animated","zoomOut","faster");
        fa_listtodo[index].addEventListener("animationend", addzoom,false);
       function addzoom(){
          if(fa_listtodo[index].classList.contains("zoomOut")){
              fa_listtodo[index].classList.replace("zoomOut","zoomIn");
               fa_listtodo[0].classList.replace("zoomOut","zoomIn");
               let temp=vm.todoitem[index];
                vm.$set(vm.todoitem,index, vm.todoitem[0]);
                vm.$set(vm.todoitem,0, temp);
          }
          else{
            fa_listtodo[index].classList.remove("zoomOut","animated","faster");
             fa_listtodo[0].classList.remove("zoomOut","animated","faster");
            fa_listtodo[index].removeEventListener('animationend', addzoom, false);
          }
       }
      }
  },computed:{
      todoitemlist(){
        return this.todoitem.filter((e,index)=>{
           return index>0&&index<4;
        });
      }
  },mouted(){
    
  }
}
</script>

<style lang="scss">
.more{
  text-align: right;
  color: #ff4384;
  cursor: pointer;
  font-weight: bold;
}
.todowrap{
  input{
    border: 0;
    width: 100%;
    color:#ff4384;
  }
  ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ff4384;
  opacity: 1; /* Firefox */
  }

  input:focus{outline:none}
  span{
    margin-left: auto;
    color:#ff4384;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
  }
  background: white;
  width: 400px;
  display: flex;
  padding: 10px 10px;
}
.item_ul{
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 200px;
  display: inline-block;
}
.itemto{
  >span{
   width: 300px;
   display: block;
   padding-left:10px; 
  }
  padding: 10px;
  border-bottom: 1px solid black;
  display: flex;
align-items: center;
}
.clock{
  color:#ff4384;
  font-size: 100px;
}
.content_left{
  background-color: #ffedf7;
  height: 100vh;
}
.content_right{
  i{
    font-size: 30px;
    color:white;
    margin: 20px 0;
    transition: .5s all;
    cursor: pointer;

  }
  i:hover{
    transform: scale(1.3);
  }
  background-color: #003164;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 50px 100px !important;
}
.timeclock{
  position: absolute;
  width:500px;
  height: 500px;
  background-color:#ff4384;
  top:50%;
  left:75%;
  transform: translate(-75%,-50%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid red;
}
.time_bt{
  background-color: #ffedf7;
  border-radius: 50%;
  width: 100px;
  display: flex;
  height: 100px;
  position: relative;
     animation: bigsize  .5s infinite ;
}
.time_bt:after{
  content: '';
  display: block;
   border-color: transparent transparent transparent#ff4384;
   border-width:20px; 
   border-style: solid;
   position: absolute;
   left: 50%;
   top:50%;
   transform: translate(calc(-50% + 10px),-50%);
   cursor: pointer;

}
.row{
  margin: 0;
}
.sotptimeclock{
  background-color: white;
}
.time_stopbt{
  background-color:#ff4384;
  animation: none;
}
.time_stopbt:after{
   content: '';
  display: block;
   border-color: white;
   border-width:0px 20px 0px 0px ; 
   border-style: double;
   position: absolute;
   left: 50%;
   top:50%;
   transform: translate(-50%,-50%);
   cursor: pointer;
   height: 25px;
  

}
.todocontent{
  margin-top: 200px;
}
.time_zero{
  width: 15px;
  height: 15px;
  background-color: #003164;
  position: absolute;
  top:62%;
  left:62%;
  display: none;
}
.time_stopzero
{
  display: block;
}
@keyframes bigsize{
  0%{
   transform: scale(1);
   
  }
  50%{
transform: scale(1.1)
  }
  100%{
transform: scale(1.3)
  }
}
//別的分頁
.itemlisttodo{
  position: absolute;
  top:0;
  left:0;
  z-index: 2;
  background-color: #003164;
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}
.listtodo_item{
  i{
    margin: 15px 0;
    color: rgba(255,255,255,.2);
    transition: .6s all;
  }
  i:hover{
    color: #ff4384;
    cursor: pointer;
  }
  span{
    padding: 0 5px;
  }
  display: flex;
  padding: 50px 50px !important;
  box-sizing: border-box;
  flex-direction: column;
  font-size: 2.25rem;
  
}
.listtodo_clock{
  margin-top: auto;
  align-self: center;
}
.listtodo_wrap{
  height: 100vh;

}
.fa-list_itemwrap{
  >h1{
     color: white;
     background-color:rgba(255,255,255,.2);
     padding: 8px 10px;
     font-size: 1.5rem;
  }
  margin-top: 20px;
 
}
.fa-list_todowrap{
  width: 100%;
}
.fa-list_content{
  padding: 50px 0px;
}
.ltemactive{

    color:#ff4384 !important;
  

}
.fa-list_itemcontent{
  overflow: auto;
  max-height: 300px;
}
.fa_list_ul{
  .fa_list_itemto{
      >span{
   width:100%;
   display: block;
   padding-left:10px; 
  }
 padding: 10px;
  border-bottom: 1px solid white;
  display: flex;
align-items: center;
  }
  list-style: none;
  padding: 0;
  margin: 0;
  color: white;
}
.item_close{
  >i{
    font-size: 1.5rem;
    color:white;
    cursor: pointer;
    transition: .5s all;
  }
  >i:hover{
     transform: scale(1.5);
  }
  text-align: right;
  padding: 50px !important;
}
.focus_wrap{
  display: flex;
}
.focustime_content{
  >h5{
    color:white;
  }
  >p{
    >span{
      font-size: 60px;
      color:#ff4384;
    }
     color:rgba(255,255,255,.2);
      font-weight: bold;
  }
  flex: 1;
}
.radiostyle{
  input{
    opacity: 0;
  }
  label{
    color: white;
    font-weight: bold;
    position: relative;
    padding-left: 20px;
  }
  label:before{
    content:"";
    position:absolute;
    left: 0;
    background-color: transparent;
    height: 1rem;
    width:  1rem;
    border: 2px solid white;
    top: 50%;
    transform: translate(0,-50%);
  }
   input:checked{
     +label:after{
      content:"";
    position:absolute;
    left: 0px;
    background-color:#ff4384;
    height: 1rem;
    width:  1rem;
    top: 50%;
     transform: translate(0%, -50%) scale(.4);
    transform-origin: center;
     }
     +label:before{
       border: 2px solid #ff4384;
     }
  }
  margin: 20px 0;
}
body{
  overflow: hidden;
}
</style>



