<template>
<div>
   <chartjs-bar class="hello" :datalabel="labels" :labels="datasets.mylabels" :data="datasets.data" :backgroundcolor="mybackgroundcolor"></chartjs-bar>
   </div>
</template>

<script>
export default {
  data(){
    return{
      labels:"date",
      mybackgroundcolor:"rgba(255,255,255,1)",
       datasets:{
         mylabels:["8/1","8/2","8/3","8/4","8/5","8/6","8/7"],
           data:[26,10,3,5,7,8,15],
           backgroundColor:["Red","Green","Blue"],

       },
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 100%;
}
</style>



